.collection-content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.collection-content {
  width: 700px;
  height: 60vh;
  max-height: 700px;
  overflow: auto;
}

.collection-title {
  display: flex;
  justify-content: center;
  padding: 35px;
  color: black;
  font-family: Roboto;
  font-weight: bold;
  font-size: 2em;
}

@media only screen and (max-width: 690px) {
  .collection-content {
    width: 300px;
  }

  .mycollection-action-button {
    justify-self: center !important;
  }
}
