i {
  cursor: pointer;
  color: rgb(223, 224, 226);
  transition: color 0.3s;
  height: auto;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: black;
}

ul {
  padding-left: 0 !important;
}

i:hover {
  color: rgb(0, 0, 0, 0.6);
}

.header-wrapper {
  position: relative;
  margin-bottom: 15px;
  z-index: 2;
}

.headerr {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: auto;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  background: rgb(7, 16, 19);
  z-index: 1;
}

.header-mobile-dropdown {
  display: none;
  height: 50px;
  width: 50px;
  margin: 10px;
  border-radius: 500px;
  background: rgb(223, 224, 226);
  transition: display 3s;
}

.header-logo {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 10px;
}

.header-logo-image {
  max-width: 100px;
  height: auto;
}

.header-logo-title {
  color: rgb(223, 224, 226);
  font-size: 3em;
  font-family: Allerta Stencil;
  padding-left: 5px;
  transition: color 1s;
}

.header-logo-title:hover {
  color: rgb(170, 170, 170);
}

.header-right-options {
  display: flex;
  padding-right: 10px;
}

.header-right-options-items {
  padding: 20px;
}

.dropdown-mobile-menu {
  visibility: visible;
  position: absolute;
  background: rgb(225, 81, 96);
  margin: 0;
  border-radius: 5px;
  width: 200px !important;
  height: 0px;
}

.menu-item {
  list-style-type: none;
  padding: 15px;
  color: black;
  font-family: Roboto;
  font-weight: bold;
  transition: 4s, background 1s, color 1s;
  cursor: pointer;
}

.menu-item:hover {
  background-color: rgb(183, 153, 156);
  color: white;
}

.menu-item-inactive {
  color: black;
  padding: 15px;
  font-family: Roboto;
  font-weight: bold;
  transition: 1s, background 1s, color 1s;
  cursor: pointer;
}

.menu-item-inactive:hover {
  background-color: rgb(183, 153, 156);
  color: white;
}

.dropdown-mobile-menu-inactive,
.dropdown-mobile-menu-active {
  visibility: visible;
  position: absolute;
  background: rgb(225, 81, 96);
  margin: 0;
  border-radius: 5px;
  width: 200px !important;
}

@media only screen and (max-width: 768px) {
  .header-logo-title {
    font-size: 2em;
  }

  .header-logo-image {
    display: none;
  }

  .header-mobile-dropdown {
    display: block;
  }

  .dropdown-mobile-menu-active {
    position: absolute;
    background: rgb(225, 81, 96);
    margin: 0;
    border-radius: 5px;
    width: 200px !important;
    min-height: 100px;
    height: 210px;
    transition: 2s;
    z-index: 1;
  }

  .dropdown-mobile-menu-inactive {
    position: absolute;
    background: rgb(225, 81, 96);
    margin: 0;
    border-radius: 5px;
    width: 200px !important;
    height: 0%;
    transition: 2s;
    z-index: 1;
  }

  .menu-item-inactive {
    visibility: hidden;
    color: black;
    padding-top: 0px;
    padding-right: 0px;
    padding-bottom: 0px;
    padding-left: 15px;
    font-family: Roboto;
    font-weight: bold;
    transition: 0.7s;
  }
}

@media only screen and (max-width: 440px) {
  .header-logo-title {
    font-size: 1.2em;
  }

  .header-right-options-items {
    padding: 10px;
  }
}
