.addmodel-container {
  padding: 30px;
  padding-top: 10px;
}

.input {
  display: flex;
  flex-direction: column;
}

.form-type-title {
  padding: 20px;
}

.addmodel-checkbox-container {
  display: flex;
}

.addmodel-checkbox-container-option {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
}

.addmodel-checkbox-container-option * {
  padding: 2px;
}

.addmodel-field {
  height: 100px;
  min-width: 400px;
  margin-right: 100px;
  margin-left: 50px;
}

.addmodel-field-label {
  font-family: Roboto;
  font-size: 1.2em;
  font-weight: bold;
}

.image-preview-container-image {
  height: auto;
  width: 100%;
  max-width: 100px;
}

.progress-indicator {
  min-width: 100% !important;
}
