.content-name {
  font-family: Roboto;
  font-weight: bold;
  font-size: 1em !important;
}

.cart-container {
  height: 100%;
}

.cart-wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.cart-gallery,
.cart-gallery-mobile {
  position: relative;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 2fr 1fr;
  padding-bottom: 40px;
}

.cart-gallery-mobile {
  display: none;
}

.item-right-floated-options {
  display: flex;
  align-items: center;
}

.summary-container {
  padding: 20px;
}

.summary-sum {
  display: flex;
  justify-content: flex-end;
  padding: 20px;
}

.summary-wrapper {
  position: relative !important;
  display: grid;
  grid-template-rows: 1fr auto;
  max-height: 640px !important;
  overflow: auto !important;
}

.trash-icon {
  color: grey;
  transition: color 0.2s;
}

.trash-icon:hover {
  color: red;
}

.subtotal-container {
  position: relative;
  padding: 20px;
  max-height: 100%;
  overflow: auto;
}

.total-segment {
  background: rgb(225, 81, 96) !important;
}

.total-subtotal,
.total-tax,
.total-discount,
.total-total {
  display: flex;
  justify-content: space-between;
}

#paypal-button-container {
  position: relative;
}

@media only screen and (max-width: 767px) {
  .item-right-floated-options {
    justify-content: space-between;
    max-width: 300px;
  }

  .cart-gallery {
    display: none;
  }

  .cart-gallery-mobile {
    display: grid;
    grid-template-columns: 1fr;
    padding: 20px;
    max-height: 640px !important;
    overflow: auto !important;
  }

  .ui.items:not(.unstackable) > .item > .image,
  .ui.items:not(.unstackable) > .item > .image > img {
    max-width: 80px !important;
  }

  .ui.items:not(.unstackable) > .item {
    flex-direction: row;
  }

  .ui.items:not(.unstackable) > .item > .image + .content {
    padding: 0 !important;
    padding-left: 1.5em !important;
  }
}
