.gallery-container {
  height: 100%;
}

.gallery-wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.log-modal,
.cart-modal,
.upload-modal {
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed; /* Stay in place */
  z-index: 5; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

.log-modal-content,
.cart-modal-content,
.upload-modal-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 30px;
  border: 1px solid #888;
  border-radius: 5px;
  height: auto;
}

.log-modal-header,
.cart-modal-header,
.upload-modal-header {
  font-family: Roboto;
  font-weight: bold;
  font-size: 1.2em;
}

.log-modal-image {
  max-width: 180px;
  height: auto;
}

.log-modal-button,
.cart-modal-button {
  padding: 10px;
}
