img {
  height: auto;
  width: 100%;
  max-width: 300px;
}

.gridcard-card-container {
  position: relative;
  overflow: auto !important;
  padding: 20px !important;
  margin-left: 200px !important;
  height: 100%;
  z-index: 1;
}

.cardgrid-card {
  display: grid !important;
  grid-template-rows: 200px 130px;
  height: 350px;
  max-width: 250px !important;
  min-width: 250px !important;
  width: 100%;
  margin-right: 10px !important;
}

.cardgrid-card-image-container {
  display: flex !important;
  justify-content: center !important;
}

.card-header {
  padding-top: 10px !important;
  padding-bottom: 0px !important;
  margin-bottom: 0px !important;
}

.content {
  justify-self: center;
}

.card-pricetag {
  padding: 10px;
  padding-left: 0;
}

.card-buttons {
  width: 100px;
  height: 35px;
}

.button-inner-text {
  font-size: 0.8em;
}

.card-grid-button-container-gallery {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.card-grid-button-container-singlebutton {
  display: flex;
  justify-content: center;
  align-items: baseline;
}

#card-acquired-button {
  background: gray !important;
}

#card-acquired-button:hover {
  background: gray !important;
}

.listing-lazy-load {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  position: relative !important;
  margin-left: 100px !important;
  z-index: -5 !important;
}

@media only screen and (max-width: 767px) {
  .cardgrid-card {
    border: 1px solid #d4d4d5 !important;
    border-radius: 0.28571429rem !important;
  }
}

@media only screen and (max-width: 767px) {
  .ui.stackable.cards {
    display: flex !important;
    justify-content: center !important;
  }
  .listing-lazy-load {
    margin-left: 0px !important;
  }
  .ui.grid > .stackable.stackable.row > .column,
  .ui.stackable.grid > .column.grid > .column,
  .ui.stackable.grid > .column.row > .column,
  .ui.stackable.grid > .column:not(.row),
  .ui.stackable.grid > .row > .column,
  .ui.stackable.grid > .row > .wide.column,
  .ui.stackable.grid > .wide.column {
    margin-bottom: 15px !important;
  }
}
