.adminwindow-container {
  display: flex;
}

.adminwindow-sidebar {
  display: flex;
  flex-direction: column;
  position: fixed;
  height: 100%;
  width: 250px !important;
  background: rgb(170, 170, 170);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.adminwindow-sidebar-header {
  display: flex;
  justify-content: center;
}

.adminwindow-sidebar-header-title {
  color: rgb(7, 16, 19);
  font-family: Allerta Stencil;
  font-size: 1.5em;
  font-weight: bolder;
  padding: 25px;
}

.adminwindow-sidebar-options {
  display: flex;
  flex-direction: column;
}

.adminwindow-sidebar-options-item {
  font-family: Roboto;
  padding: 15px;
  padding-left: 20px;
  border-top: 1px solid rgb(223, 224, 226);
  cursor: pointer;
  color: rgb(223, 224, 226);
  transition: background 1s, color 1s;
}

.adminwindow-sidebar-options-item:hover {
  background: rgb(235, 81, 96);
  color: rgb(223, 224, 226);
}

.adminwindow-content {
  display: flex;
  flex-direction: column;
  margin-left: 250px;
  width: 100%;
}
