#page-footer {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  background: grey;
  height: 80px;
  border-radius: 5px;
  width: 100%;
  margin-top: 70px;
  z-index: 2;
}

.footer-middle-column {
  display: grid;
  grid-template-rows: 1fr 1fr;
  text-align: center;
  color: white;
  font-family: Roboto;
}

#facebook-icon {
  font-size: 2.5em;
}

@media only screen and (max-width: 768px) {
  .footer-middle-column {
    font-size: 0.8em;
  }

  #facebook-icon {
    font-size: 2em;
  }
}

@media only screen and (max-width: 346px) {
  .footer-middle-column {
    font-size: 0.6em;
  }

  #facebook-icon {
    font-size: 1.5em;
  }
}
