.account-dropdown-menu {
  position: absolute;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  background: rgb(225, 81, 96);
  border-radius: 5px;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  width: 180px;
  height: 110px;
  min-height: 0px;
  transition: 0.2s;
  z-index: 3;
}

.account-dropdown-menu-inactive {
  position: absolute;
  float: right;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  background: rgb(225, 81, 96);
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  height: 0;
  width: 180px;
  transition: 1s;
  z-index: 3;
}

.account-dropdown-items {
  visibility: hidden;
  padding: 15px;
  color: black;
  font-family: Roboto;
  font-weight: bold;
  transition: background 1s;
  cursor: pointer;
}

.account-dropdown-items:hover {
  background: rgb(183, 153, 156);
  color: white;
}
